import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect, useState } from 'preact/hooks';
import { SerializationHelpers } from '../../stores/helper'
import { Chart } from "react-google-charts";
import Total from '../../components/Total';

const Analysis = () => {
    const [showAlertData, setShowAlertData] = useState(false);
    const [monthlyExpensesData, setMonthlyExpensesData] = useState([] as CategoryType[]);
    const [yearlyTotalData, setYearlyTotalData] = useState(0);
    const [yearlyCashbackData, setYearlyCashbackData] = useState(0);
	const [expenseChartData, setExpenseChartData] = useState([['category', 'amount']]);
    const [top10ChartData, setTop10ChartData] = useState([['item', 'amount']]);
    const [flowData, setFlowData] = useState([] as any[][]);

    useEffect(() => {
		let monthlyExpenses = Stores.GetTotalSpendingAsMonthlyExpense()
        
        setMonthlyExpensesData(monthlyExpenses.category)
        setExpenseChartData(getExpenseChartData(monthlyExpenses.category))
        setTop10ChartData(getTop10ExpenseChartData(monthlyExpenses.items, parseInt(monthlyExpenses.expense)))

        if(monthlyExpenses.category.length > 0) {
            let total = monthlyExpenses.category[monthlyExpenses.category.length-1]

            setYearlyTotalData((parseInt(total.expense) * 12))
            setYearlyCashbackData((parseInt(total.cashback) * 12)+ Stores.info.card_bonus.amt.value)
        }

        setFlowData(getExpenseFlowChartData(monthlyExpenses))
	}, []);


    const getExpenseFlowChartData = (monthlyExpenses: any) => {
        let categories = monthlyExpenses.category
        let result: any[][] = []
        result.push(['From', 'To', '$'])

        for(let i=0; i<categories.length-1; ++i) {
            let category = categories[i]
            let amt = parseInt(category.expense)
            if(amt > 0) {
                let title = SerializationHelpers.CategoryTitleTransform(category.category)
                result.push(['Expenses', title , amt * 12])

                if(category.children.length > 0) {
                    let items = category.children

                    for(let j=0; j<items.length; ++j) {
                        let item = items[j]
                        if(item.expense > 0) {
                            if(item.cashback > 0) {
                                result.push([title, 'cash back' , item.cashback * 12])
                                result.push([title, item.item, (item.expense-item.cashback) * 12])
                            } else {
                                result.push([title, item.item, item.expense * 12])
                            }
                            
                        }
                    }
                }
            }
        }
        // net pay is set
        if(Stores.info.net_pay.amt.value > 0) {
            let net_pay = Stores.info.net_pay.amt.value * 12
            let total_expense = parseInt(monthlyExpenses.expense) * 12
            result.push(['Net Pay', 'Expenses',  total_expense])
            result.push(['Net Pay', 'Savings',  net_pay - total_expense])

        }
        if(Stores.info.mortgage_principal.amt.value > 0) {
            let frequency = Stores.info.mortgage_principal.frequency.value
            let amt = Stores.info.mortgage_principal.amt.value
            switch(frequency) {
                case 0: amt *= 12; break;
                case 1: amt *= 4; break;
                case 2: amt *= 2; break;
                default: break;
            }
            result.push(['Net Pay', 'Principal Accumulation', amt])

        }
        return result
    }

    const getTop10ExpenseChartData = (items : any[], total_expense : number) => {
        const max: number = 10

        let result: any = []
		result.push(['item', 'amount'])
		
        items.sort((a, b) => {
            return parseInt(b.expense) - parseInt(a.expense)
        })
        let top10_sum = 0

		for(let i=0; i<items.length-1; ++i) {
            if(i === max) break
            let item = items[i]
            top10_sum += parseInt(item.expense)
            result.push([item.item, parseInt(item.expense)])
        }
        result.push(['All others', total_expense - top10_sum])

        return result
    }

    const getExpenseChartData = (categories: any) => {
        let result: any = []
		result.push(['category', 'amount'])
		
		for(let i=0; i<categories.length-1; ++i) {
            let category = categories[i]
            result.push([SerializationHelpers.CategoryTitleTransform(category.category), parseInt(category.expense)])
        }
        return result
    }

    const showSuccessAlert = () => {
		if(showAlertData) {
			return (
				<div id="bs-alert" class="alert alert-success alert-dismissible fade show" role="alert">
						Your estimated expense model is saved.
					<button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
			  	</div>
			)
		}
	}

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>
			<div class="container">
            <h6>Annual Expense Flow</h6>
            <hr/>
            <Chart
                chartType="Sankey"
                width="1000px"
                height="600px"
                data={flowData}
                
                />
				<hr/>
				<button type="button" class="btn btn-lg btn-primary" onClick={() => {Stores.Serialize(); setShowAlertData(true)}}>Save Model locally</button>
				&nbsp;&nbsp;
				<button type="button" class="btn btn-lg btn-primary" onClick={() => {Stores.ResetModel(); route('/expense/basic');}}>Reset Model</button>
				<hr/>                

				<h6>Monthly Expense Analysis</h6>
                <div class="row align-items-start">
					<div class="col">
                        <table class="table table-bordered" style="width:450px;">
                            <thead>
                                <tr>
                                    <th scope="col">Category</th>
                                    <th scope="col">Expense</th>
                                    <th scope="col">Cashback</th>
                                </tr>
                            </thead>
                            <tbody style="font-size:9.5pt;">
                                {monthlyExpensesData.map(category => <tr><td>{SerializationHelpers.CategoryTitleTransform(category.category as any)}</td><td class="text-end">{category.expense}</td><td class="text-end">{category.cashback}</td></tr>)}
                            </tbody>
                        </table>
                        <hr/>
				        <h6>Cashback over Expenses (%)</h6>
                        <h1><b>{((yearlyCashbackData / yearlyTotalData) * 100).toFixed(1)}%</b></h1>     
                        <label style="font-size:9pt;">This is the percentage of savings on your total expenses. Generally anything more than 2% is considered very good.</label>
                        <hr/>         
					</div>
					<div class="col">
                        <Total value={yearlyTotalData} title="Est. Annual Expenses" 
	    						description="Estimated annual expenses."/>
                        <Total value={yearlyCashbackData} title="Est. Annual Cashback" 
	    						description="Estimated annual cashback. This is normally achieved with credit card points. Some credit cards offer perks up to 10% on some spending categories. This also includes credit card bonus for the year, if any."/>
                        <Chart
                                chartType="PieChart"
                                data={expenseChartData}
                                options={
                                    {
                                        title: "Monthly Category Expense",
                                        pieHole: 0.5,
                                        is3D: false,
                                        titleTextStyle: {
                                            fontName: 'Helvetica',
                                            fontSize: 14,
                                        }
                                    }
                                }
                                width={"450px"}
                                height={"350px"}/>        
                        <Chart
                                chartType="PieChart"
                                data={top10ChartData}
                                options={
                                    {
                                        title: "Monthly Top 10 Expenses",
                                        pieHole: 0.5,
                                        is3D: false,
                                        titleTextStyle: {
                                            fontName: 'Helvetica',
                                            fontSize: 14,
                                        }
                                    }
                                }
                                width={"450px"}
                                height={"350px"}/>                                                   
					</div>
				</div>
                
            </div>
        </div>)
}

export default Analysis;
    